.contenedor2{
    background-color: $fondoOscuro;
    width: 100%;;

    h2{
        padding: 2rem 0 0 0;
        width: 100%;
        color: #fff;    
        font-family: $fuenteHeading;
        font-weight: 400;
        text-transform: uppercase;
    }
}
.galeria{
    width: 80%;
    margin: 0 auto;
    padding-bottom: 3rem;   
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;

   
    .contenedor_img{
        overflow: hidden;
        display: flex;
        flex-direction: column;
     

        img{
            height: 100%;
            transition: all .5s;
            border-bottom: 1px solid $principal;
        }
        .text{
            background-color: $fondoClaro;
            width: 100%;
            padding: 1rem;
            transition: all .5s; 
            display: flex;
            flex-direction: column;
            align-items: center;

            h3{
                color: #fff;
                padding: .5rem 0 1rem 0;
                margin: 0;
                font-family: $fuenteHeading;
                font-weight: 200;
            }
            a{
                background-color: $fondoOscuro;
                padding: .5rem 1rem;
                border: $principal solid 1px;
                color: #fff;
                text-align: center;
                margin-bottom: .5rem;
            }
            a:hover{
                background-color: $principal;
                border: #fff solid 1px;
                color: #000;
            }
            a:active{
                transform: translateY(3px)
            }
        }

    }
}