.acerca-de {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  background-color: $fondoClaro;
  padding: 15rem 5rem 2rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  h2{
    color: $principal;
    font-size: 3rem;
    font-family: $fuente_principal;
    text-transform: uppercase;
    font-weight: 200;        
    letter-spacing: 10px;    
   }

  .linea-1 {
    width: 50%;
    height: 2px;
    background-color: $principal;
    margin: 0 auto 15px auto;
  }
  .linea-2 {
    width: 30%;
    height: 2px;
    background-color: $principal;
    margin: 0 auto;
  }

  a {
    color: $principal;
    border: 1px solid $principal;
    display: block;
    transform: translateY(25px);
    padding: 0.1rem 0.5rem;
    text-decoration: none;
    transition: all 0.5s;

    @include tablet{
        width: 20%;
        margin:  0 auto;
    }
  }
  a:hover {
    background-color: #fff;
    color: #000;
  }

  p{
      font-size: 1.5rem;

      @include tablet{
          font-size: 2.2rem;
      }
  }

  
}
.servicos{
    
    h3{
      color: $principal;
      font-size: 3rem;
      font-family: $fuente_principal;
      text-transform: uppercase;
      margin-top: 3rem;
    }
  
    .servcios_contenedor{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
    @include tablet{
        
    }
    .servico_tecnoogia{
        margin: 0 1rem;

        text-align: center;
        color: #fff;
        font-size: 10rem;
        cursor: pointer;
        transition: all .5s ease-in-out;

        p{
            color: $principal;
            margin: 0;
            text-align: center;
        }   
        
        &:hover{
            color: $principal;
        }
    }
}
