.bloqueado{
    position: fixed;
    width: 100%;
    padding: 1.5rem 5rem;
    top: 0%;
    left: 0%;
    display: flex;
    justify-content: flex-end;
    background-color: $fondoOscuro;

    ul{
        display: flex;
    }

    animation: transisicion  .8s;
}
.desbloqueado{
 animation: transisicion2 .8s  ;
}

@keyframes transisicion{
    from{
        top: -100px;
    }
    to{
        top: 0px;
    }
}
@keyframes transisicion2{
    from{
        top: 0px;
    }
    to{
        top: -100px;
    }
}

.selecionado{
    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0px;
        width: 100%;
        height: 2px;
        background-color: $principal;
        display: block;
        transition: all .5s;

    }

    &::after{
        content: '';
        position: absolute;
        bottom: -5px;
        right: 0px;
        width: 100%;
        height: 2px;
        background-color: $principal;
        margin: 5px 0;    
        display: block;
        transition: all .5s;
    }
      
}

