.boton{
    position: relative;
    overflow: hidden;

     
    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: -190px;
        width: 100%;
        height: 2px;
        background-color: #fff;
        display: block;
        
        transition: all .5s;

    }
    &:hover:before{ 
        left: 0%;
        background-color: $principal;
    }

    &::after{
        content: '';
        position: absolute;
        bottom: -5px;
        right: -190px;
        width: 100%;
        height: 2px;
        background-color: #fff;
        margin: 5px 0;    
        display: block;
        transition: all .5s;

    }

    &:hover::after{ 
        right: 0px;
        background-color: $principal;
    }


 
  span:before{
  content: "";
  position: absolute;
  top: -40px;
  left: 0%;
  width: 2px;
  height: 100%;
  margin: auto 0 ;
  background-color: #fff;

  transition: all 0.5s;
  }       

  &:hover span:before {
    top: 0px;
    background-color: $principal;
  }

  span::after{
    content: "";
    position: absolute;
    top: 40px;
    right: 0px;
    width: 2px;
    height: 100%;
    margin: auto 0 ;
    background-color: #fff;
  
    transition: all 0.5s;
    }       
  
    &:hover span::after {
      background-color: $principal;
      top: 0;
    }
  
}    
