  text-align: center;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}

p {
  color: #fff;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Rubik", sans-serif;
  margin: 0 0 1rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

.header {
  background-color: #111112;
  max-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
}

.header::before {
  content: '';
  background-image: linear-gradient(#111112af, #11111250), url("/build/img/Baner-01.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  display: block;
  height: 100vh;
  width: 49vw;
}

.header::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0%;
  background-color: rgba(24, 25, 26, 0.7);
  display: block;
  height: 100vh;
  width: 49vw;
}

.contenido {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  top: 0%;
  padding: 3rem;
  width: 100%;
  z-index: 5;
}

.contenido .navegacion {
  display: flex;
  justify-content: space-between;
}

.contenido .navegacion ul {
  display: flex;
  list-style: none;
  padding: 0%;
}

@media (min-width: 768px) {
  .contenido .navegacion ul {
    display: flex;
    margin: 0;
    padding: 0;
  }
}

@media (min-width: 1024px) {
  .contenido .navegacion ul {
    margin: 5px 30px 5px 0 !important;
    padding: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
  }
}

.contenido .navegacion a {
  color: #fff;
  transition: all .5s;
}

.contenido .navegacion .link:hover {
  color: #988345;
}

@media (min-width: 768px) {
  .navegacion_link {
    margin-right: -30px;
  }
}

.navegacion {
  width: 100%;
}

@media (max-width: 768px) {
  .navegacion {
    position: relative;
  }
  .navegacion .navegacion_link {
    position: absolute;
    width: 100%;
    justify-content: center;
    font-size: 2rem;
    top: 0px;
    left: 0%;
  }
  .navegacion .navegacion_link li {
    margin: .5rem .8rem;
  }
  .navegacion .navegacion_redes {
    position: absolute;
    width: 100%;
    justify-content: space-evenly;
    margin: 0 auto;
    top: 60px;
    left: 0;
    font-size: 3rem;
  }
  .navegacion .navegacion_redes li {
    margin: .5rem 1rem;
  }
}

@media (min-width: 768px) {
  .navegacion li {
    font-size: 3rem;
    padding: .2rem 1rem;
  }
}

.contenido_principal {
  margin-top: 20rem;
  max-width: 100%;
}

@media (min-width: 768px) {
  .contenido_principal {
    margin-top: 10rem;
  }
}

.contenido_principal h1 {
  font-family: "Chakra Petch", sans-serif;
  color: #988345;
  margin: 0;
}

.contenido_principal p {
  margin: 1rem 0 0 0;
  text-align: center;
  font-size: 3rem;
}

.contenido_principal .logo {
  display: flex;
}

.contenido_principal .logo img {
  align-self: center;
  margin: auto;
}

@media (min-width: 480px) {
  .contenido_principal h1 {
    font-size: 8rem;
  }
}

@media (min-width: 768px) {
  .contenido_principal {
    margin-top: 12rem;
  }
  .contenido_principal h1 {
    font-size: 9rem;
  }
}

.flecha {
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
}

.flecha .lado1 {
  position: absolute;
  top: 50px;
  font-size: 5.5rem;
  color: #988345;
  text-shadow: 0 0 2rem rgba(255, 255, 255, 0.795);
  animation: float .9s ease  infinite;
}

@keyframes float {
  0% {
    top: 0px;
  }
  20% {
    top: 2px;
  }
  30% {
    top: 4px;
  }
  40% {
    top: 6px;
  }
  50% {
    top: 6px;
  }
  60% {
    top: 4px;
  }
  70% {
    top: 2px;
  }
  80% {
    top: 0px;
  }
  90% {
    top: -2px;
  }
  100% {
    top: 0px;
  }
}

.boton {
  position: relative;
  overflow: hidden;
}

.boton::before {
  content: '';
  position: absolute;
  top: 0;
  left: -190px;
  width: 100%;
  height: 2px;
  background-color: #fff;
  display: block;
  transition: all .5s;
}

.boton:hover:before {
  left: 0%;
  background-color: #988345;
}

.boton::after {
  content: '';
  position: absolute;
  bottom: -5px;
  right: -190px;
  width: 100%;
  height: 2px;
  background-color: #fff;
  margin: 5px 0;
  display: block;
  transition: all .5s;
}

.boton:hover::after {
  right: 0px;
  background-color: #988345;
}

.boton span:before {
  content: "";
  position: absolute;
  top: -40px;
  left: 0%;
  width: 2px;
  height: 100%;
  margin: auto 0;
  background-color: #fff;
  transition: all 0.5s;
}

.boton:hover span:before {
  top: 0px;
  background-color: #988345;
}

.boton span::after {
  content: "";
  position: absolute;
  top: 40px;
  right: 0px;
  width: 2px;
  height: 100%;
  margin: auto 0;
  background-color: #fff;
  transition: all 0.5s;
}

.boton:hover span::after {
  background-color: #988345;
  top: 0;
}

.acerca-de {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  background-color: #111112;
  padding: 15rem 5rem 2rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.acerca-de h2 {
  color: #988345;
  font-size: 3rem;
  font-family: "Rubik", sans-serif;
  text-transform: uppercase;
  font-weight: 200;
  letter-spacing: 10px;
}

.acerca-de .linea-1 {
  width: 50%;
  height: 2px;
  background-color: #988345;
  margin: 0 auto 15px auto;
}

.acerca-de .linea-2 {
  width: 30%;
  height: 2px;
  background-color: #988345;
  margin: 0 auto;
}

.acerca-de a {
  color: #988345;
  border: 1px solid #988345;
  display: block;
  transform: translateY(25px);
  padding: 0.1rem 0.5rem;
  text-decoration: none;
  transition: all 0.5s;
}

@media (min-width: 768px) {
  .acerca-de a {
    width: 20%;
    margin: 0 auto;
  }
}

.acerca-de a:hover {
  background-color: #fff;
  color: #000;
}

.acerca-de p {
  font-size: 1.5rem;
}

@media (min-width: 768px) {
  .acerca-de p {
    font-size: 2.2rem;
  }
}

.servicos h3 {
  color: #988345;
  font-size: 3rem;
  font-family: "Rubik", sans-serif;
  text-transform: uppercase;
  margin-top: 3rem;
}

.servicos .servcios_contenedor {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.servicos .servico_tecnoogia {
  margin: 0 1rem;
  text-align: center;
  color: #fff;
  font-size: 10rem;
  cursor: pointer;
  transition: all .5s ease-in-out;
}

.servicos .servico_tecnoogia p {
  color: #988345;
  margin: 0;
  text-align: center;
}

.servicos .servico_tecnoogia:hover {
  color: #988345;
}

.contenedor2 {
  background-color: rgba(24, 25, 26, 0.7);
  width: 100%;
}

.contenedor2 h2 {
  padding: 2rem 0 0 0;
  width: 100%;
  color: #fff;
  font-family: "Chakra Petch", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
}

.galeria {
  width: 80%;
  margin: 0 auto;
  padding-bottom: 3rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.galeria .contenedor_img {
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.galeria .contenedor_img img {
  height: 100%;
  transition: all .5s;
  border-bottom: 1px solid #988345;
}

.galeria .contenedor_img .text {
  background-color: #111112;
  width: 100%;
  padding: 1rem;
  transition: all .5s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.galeria .contenedor_img .text h3 {
  color: #fff;
  padding: .5rem 0 1rem 0;
  margin: 0;
  font-family: "Chakra Petch", sans-serif;
  font-weight: 200;
}

.galeria .contenedor_img .text a {
  background-color: rgba(24, 25, 26, 0.7);
  padding: .5rem 1rem;
  border: #988345 solid 1px;
  color: #fff;
  text-align: center;
  margin-bottom: .5rem;
}

.galeria .contenedor_img .text a:hover {
  background-color: #988345;
  border: #fff solid 1px;
  color: #000;
}

.galeria .contenedor_img .text a:active {
  transform: translateY(3px);
}

footer {
  background-color: #111112;
  padding: 3rem;
}

footer p {
  margin: 0;
  text-align: center;
}

footer p span {
  color: #988345;
}

.bloqueado {
  position: fixed;
  width: 100%;
  padding: 1.5rem 5rem;
  top: 0%;
  left: 0%;
  display: flex;
  justify-content: flex-end;
  background-color: rgba(24, 25, 26, 0.7);
  animation: transisicion  .8s;
}

.bloqueado ul {
  display: flex;
}

.desbloqueado {
  animation: transisicion2 .8s;
}

@keyframes transisicion {
  from {
    top: -100px;
  }
  to {
    top: 0px;
  }
}

@keyframes transisicion2 {
  from {
    top: 0px;
  }
  to {
    top: -100px;
  }
}

.selecionado::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0px;
  width: 100%;
  height: 2px;
  background-color: #988345;
  display: block;
  transition: all .5s;
}

.selecionado::after {
  content: '';
  position: absolute;
  bottom: -5px;
  right: 0px;
  width: 100%;
  height: 2px;
  background-color: #988345;
  margin: 5px 0;
  display: block;
  transition: all .5s;
}

.contenedor-ventana {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 20%;
  left: -100%;
  z-index: 100;
}

@media (min-width: 768px) {
  .contenedor-ventana {
    top: 0;
    display: grid;
    place-items: center;
  }
}

.contenedor-ventana .ventana {
  background-color: #FFF;
  max-width: 90%;
  padding: 2rem;
  margin: 0 auto;
  border-radius: .5rem;
  border: 1px solid #988345;
  visibility: hidden;
  z-index: 5;
  position: relative;
}

@media (min-width: 768px) {
  .contenedor-ventana .ventana {
    max-width: 50%;
  }
}

.contenedor-ventana .ventana h2 {
  color: #988345;
  font-size: 3rem;
  margin-bottom: 1.5rem;
  font-family: "Chakra Petch", sans-serif;
}

.contenedor-ventana form {
  display: flex;
  flex-wrap: wrap;
}

.contenedor-ventana form input {
  margin: 1rem .5rem;
  padding: .5rem 1rem;
  color: #988345;
  font-size: 2rem;
  border: none;
  border-bottom: 1px solid #111112;
}

.contenedor-ventana form input[type=text] {
  flex: 1 1 auto;
}

.contenedor-ventana form input[type=email] {
  flex: 0 1 100%;
}

.contenedor-ventana form button {
  flex: 0  1 40%;
  margin: 1rem auto;
  padding: .5rem 1rem;
  border: none;
  color: #FFF;
  background-color: #111112;
  cursor: pointer;
}

.contenedor-ventana form textarea {
  width: 95%;
  margin: 1rem auto;
  border: 1px solid #111112;
}

.contenedor-ventana form label {
  color: #111112;
  margin: 1rem 0 0 1rem;
  font-size: 2rem;
}

.visible {
  left: 0;
}

.visibleVentana {
  visibility: visible !important;
  animation: anim .5s;
}

@keyframes anim {
  0% {
    left: -10%;
  }
  20% {
    left: 0;
  }
  50% {
    left: 10%;
  }
  70% {
    left: 20%;
  }
  90% {
    left: 10%;
  }
  100% {
    left: 0%;
  }
}
