.contenedor-ventana{
    
    position: fixed;
    width: 100%;
    height: 100%;
    top: 20%;
    left: -100%;
    z-index: 100;

    @include tablet{
       top: 0;
       display: grid;
       place-items: center;  
    }
    

    
    .ventana{
        
        background-color:#FFF;
        max-width: 90%;
        padding: 2rem;
        margin: 0 auto;
        border-radius: .5rem;
        border: 1px solid $principal;
        visibility: hidden;
        z-index: 5;    
        position: relative;
    
        
        @include tablet{
            max-width: 50%;
        }
        
        h2{
            color: $principal;
            font-size: 3rem;
            margin-bottom: 1.5rem;
            font-family: $fuenteHeading;
        }

    }
    
    form{

        display: flex;
        flex-wrap: wrap;

        input{
            margin: 1rem .5rem ;
            padding: .5rem 1rem;
            color: $principal;
            font-size: 2rem;
            border: none;
            border-bottom: 1px solid $fondoClaro;
        }
        input[type = text]{
            flex: 1 1 auto;    
        }
        input[type = email]{
            flex: 0 1 100%;
        }
        button{
            flex: 0  1 40% ;
            margin: 1rem auto;
            padding: .5rem 1rem;
            border: none;
            color: #FFF;
            background-color: $fondoClaro;
            cursor: pointer;
        }

        textarea{
            width: 95%;
            margin: 1rem auto;
            border: 1px solid $fondoClaro;
        }
        label{
            color: $fondoClaro;
            margin: 1rem 0 0 1rem;
            font-size: 2rem;
        }
    }

}


.visible{
    left: 0;
}
.visibleVentana{
    visibility: visible !important;
    animation: anim .5s;
}
@keyframes anim {
    0% {
        left: -10%;
    }
    20%{
        left: 0;
    }
    50% {
        left: 10%,
    }
    70% {
        left:20%;
    }
    90% {
        left: 10%;
    }
    100% {
        left: 0%;
    }
}