.header{
    background-color: $fondoClaro;
    max-height: 100vh;
    position: relative;
    display: flex;
    flex-direction: column;

    &::before{
        content: '';
        background-image: linear-gradient(#111112af, #11111250) ,url('/build/img/Baner-01.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        display: block;
        height: 100vh;
        width: 49vw; 
    }
    &::after{
        content: '';
        position: absolute;
        top: 0;
        right: 0%;
        background-color: $fondoOscuro;
        display: block;
        height: 100vh;
        width: 49vw;
    }

}

.contenido{
    position: absolute;
    display: flex;
    flex-direction: column;  
    align-items: center;
    gap: 3rem;
    top: 0%;
    padding: 3rem;
    // max-height: 100vh; 
    width: 100%;
    z-index: 5;

    .navegacion{
        display: flex;
        justify-content: space-between;


        ul{
            display: flex;
             list-style: none;
             padding: 0%;

            @include tablet {
                display: flex;
                margin: 0;
                padding: 0;
            }
            @include desktop{
                margin: 5px 30px 5px 0 !important;
                padding: 0;
                display: flex;
                justify-content: space-around;
                align-items: center;
                list-style: none;   

         
            
            }

        }              
        a{ 
            color: #fff; 
            transition: all .5s ;
        }

        .link:hover{
            color: $principal;
        }

    }
}
.navegacion_link{
    @include tablet{    
        margin-right:  -30px ;  
                
    }
}

.navegacion{
    width: 100%;

    
    
    @include maxWidth{
        position: relative;

        .navegacion_link{
            position: absolute;
            width: 100%;
            justify-content: center;
            font-size: 2rem;
            top: 0px;
            left: 0%;

            li{
                margin: .5rem .8rem;
            }

        }
        .navegacion_redes{
            position: absolute;
            width: 100%;
            justify-content: space-evenly;
            margin: 0 auto;
            top: 60px;
            left: 0;
            font-size: 3rem;
            
            li{
                margin: .5rem 1rem;
            }
        }
    }

    @include tablet{
        li{
            font-size: 3rem;
            padding: .2rem 1rem;
        }
    }
    
}

.contenido_principal{

    margin-top: 20rem;
    max-width: 100%;

    @include tablet{
        margin-top: 10rem;
    }

    h1{
        font-family: $fuenteHeading;
        color: #988345; 
        margin: 0;    
    }
    p{
        margin: 1rem 0 0 0;
        text-align: center;
        font-size: 3rem;
    }
    .logo{
        display: flex;
        img{
            align-self: center;
            margin: auto;
        }
    }
    @include telefono{
        h1{ font-size: 8rem;}
    }
    @include tablet{
        margin-top: 12rem;
        h1{ font-size: 9rem;}
    }

}

.flecha{
    width: 100%;
    display: flex;
    justify-content: center;
    position: relative;

    .lado1{
        position: absolute;
        top: 50px;
        font-size: 5.5rem;
        color:#988345;
        text-shadow: 0 0 2rem rgba(255, 255, 255, 0.795);
        animation: float .9s ease  infinite ;
    }
   
}
@keyframes float {
    0%{
        top: 0px;
    }
    20%{
        top: 2px;
    }
    30%{
        top: 4px;
    }
    40%{
        top: 6px;
    }
    50%{
        top: 6px;
    }
    60%{
        top: 4px;
    }
    70%{
        top: 2px;
    }
    80%{
        top: 0px;
    }
    90%{
        top: -2px;
    }
    100%{
        top: 0px;
    }
}